import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { NotifyService } from '@modules/release-notify/shared';
import { Library, Helper } from '@core/index';

@Component({
  selector: 'app-declaration',
  templateUrl: './declaration.component.html',
  styleUrls: ['./declaration.component.scss']
})
export class DeclarationComponent implements OnInit {

  rowData: any;
  frmData: any = {
    province_code: "",
    province_name: "",
    district_code: "",
    district_name: "",
    commune_name: "",
    commune_code: "",
    home_address: "",
  };
  provinces: any;
  districts: any;
  communes: any;
  constructor(
    public bsModalRef: BsModalRef,
    private notifyService: NotifyService,
  ) { }

  ngOnInit() {
  }

  provinceChange = (e) => {
    Library.showloading();
    let sub = this.notifyService.getListtypeExpandFilter({
      listtype_code: "DM_QUAN_HUYEN_CQHC",
      tag_name: "tinh_thanh",
      tag_value: e.value,
    }).subscribe(rs => {
      if (rs.status == "success") {
        this.districts = rs.data.lists
        this.communes = [];
      } else {
        Library.notify(rs.message, "error");
      }
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }

  districtChange = (e) => {
    Library.showloading();
    let sub = this.notifyService.getListtypeExpandFilter({
      listtype_code: "DM_XA_PHUONG_CQHC",
      tag_name: "quan_huyen",
      tag_value: e.value,
    }).subscribe(rs => {
      if (rs.status == "success") {
        this.communes = rs.data.lists
      } else {
        Library.notify(rs.message, "error");
      }
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }

  onSubmit(e) {
    let province_name = "", district_name = "", commune_name = "";
    this.provinces.some(item => {
      if (item.code == this.frmData.province_code) {
        province_name = item.name;
        return true;
      }
    });
    this.districts.some(item => {
      if (item.code == this.frmData.district_code) {
        district_name = item.name;
        return true;
      }
    });
    this.communes.some(item => {
      if (item.code == this.frmData.commune_code) {
        commune_name = item.name;
        return true;
      }
    });
    var data = {
      id: this.rowData.id,
      code: "1",
      province_code: this.frmData.province_code,
      province_name: province_name,
      district_code: this.frmData.district_code,
      district_name: district_name,
      commune_name: commune_name,
      commune_code: this.frmData.commune_code,
      home_address: this.frmData.home_address,
    };

    Library.showloading();
    let sub = this.notifyService.xuatToKhai(data).subscribe(rs => {
      if (rs.status == "success") {
        console.log(rs);
        let data = rs.data.invoice_release;
        // let file_content = atob(data.file_content);
        // Helper.download("1" + data.file_name, file_content);
        Helper.dowloadFile(data.file_name, 'zip', data.file_content);
      } else {
        Library.notify(rs.message, "error");
      }
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }

}
