import { Component, OnInit, ViewChild } from '@angular/core';
import { forkJoin } from "rxjs/observable/forkJoin";
import { mergeMap } from 'rxjs/operators';

import { NotifyService } from '@modules/release-notify/shared';
import { VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AddComponent } from '../add/add.component';
import { CancelNumberComponent } from '../cancel-number/cancel-number.component';
import { DeclarationComponent } from '../declaration/declaration.component';
import { Library, CoreService, Helper, AuthService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
import { String, StringBuilder } from 'typescript-string-operations';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { DxDataGridComponent } from 'devextreme-angular';

@Component({
  selector: 'app-release-notify-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  pageSize:any;
  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  templates: any;
  _translate: any;
  clickCurent = "";
  extras = []
  statusFilter = [];
  bsModalRef: BsModalRef;
  isTT78: boolean = false;
  constructor(
    private authService: AuthService,
    private vatService: VatService,
    private notifyService: NotifyService,
    private modalService: BsModalService,
    private coreService: CoreService,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());

    this.statusFilter = VSelectBoxService._trangThaiPhatHanh(this._translate);
  }
  ngOnInit() {
    this.loadList();
    this.loadTemplates();
    let infoDangKy = this.authService.getInfoRegister();
    if (infoDangKy && infoDangKy.id) {
      if (infoDangKy.status == "CHAP_NHAN")
        this.isTT78 = true;
    }
    this.extras = [{
      id: "1",
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        {
          id: "XOA",
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: "KET_XUAT_EXCEL",
          name: this._translate.CONTROLL.BUTTON.export_excel,
          icon: this._translate.ICONS.excel
        }
      ]
    }];
  }
  itemExtrasClick(e) {
    if (e.itemData.id == 'XOA') {
      this.delete(null);
    } else if (e.itemData.id == 'KET_XUAT_EXCEL') {
      this.xuatExcel(null);
    }
  }
  loadList() {
    this.dataSource = {};
    this.dataSource.store = this.notifyService.getAll();
  }

  loadTemplates() {
    let sub = this.coreService.getTemplateOption().subscribe(rs => {
      this.templates = rs;
    }, err => { }, () => {
      sub.unsubscribe();
    });
  }
  myFunction(e) {
    if (this.clickCurent != "" && this.clickCurent != e) {
      document.getElementById(this.clickCurent).classList.remove('show');
      // window.onclick = function (o) {
      //   // if (!o.target.matches('.dropbtn')) {
      //   var myDropdown = document.getElementById("dropdown");
      //   if (myDropdown.classList.contains('show')) {
      //     myDropdown.classList.remove('show');
      //   }
      //   // }
      // }
    }
    document.getElementById(e).classList.toggle("show");
    this.clickCurent = e;

  }
  // Chọn đối tượng trong màn hình danh sách
  selectionChanged(data: any) {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    this.selectedItems = data.selectedRowsData;
  }

  setNumberInvoice(data) {
    return String.Format(this._translate.CONTROLL.LABEL.so_dang_ky, `${data.amount} (${data.begin_number} - ${data.end_number})`, `${data.used_amount}`);
  }

  formatNumberInvoice(numberInvoice) {
    return Helper.formatNumberInvoice(numberInvoice);
  }

  add() {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    Library.showloading();

    let sub = this.notifyService.getTemplatesVer2().subscribe(rs => {
      let data = [];
      rs.data.templates.forEach(item => {
        data.push({
          name: `${item.invoice_type_name} - ${item.template_code} - ${item.invoice_series}`,
          value: item.id,
          end_number: parseInt(item.end_number) || 0,
          invoice_series: item.invoice_series,
          template_code: item.template_code,
          is_decree_new: item.is_decree_new
        });
      });
      if (data && data.length > 0)
        data = data.filter(p => p.is_decree_new != true);
      const initialState = {
        templates: data
      };

      this.bsModalRef = this.modalService.show(AddComponent, { class: 'modal-lg', initialState });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          this.loadList();
        }
      });
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  edit(data) {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    // if (this.selectedItems[0].release_status == "CHUA_TB_THUE") {
    Library.showloading();
    let id = data ? data.id : this.selectedRows[0];
    let sub = this.notifyService.showVer2(id).subscribe(rs => {
      let data = [];
      rs[1].data.templates.forEach(item => {
        data.push({
          name: `${item.invoice_type_name} - ${item.template_code} - ${item.invoice_series}`,
          value: item.id,
          end_number: parseInt(item.end_number) || 0,
          invoice_series: item.invoice_series,
          template_code: item.template_code,
          is_decree_new: item.is_decree_new
        });
      });
      if (data && data.length > 0)
        data = data.filter(p => p.is_decree_new != true);
      const initialState = {
        rowData: rs[0].data.invoice_release,
        templates: data
      };
      this.bsModalRef = this.modalService.show(AddComponent, { class: 'modal-lg', initialState });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          this.loadList();
        }
      });
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
    // } else {
    //   Library.notify("Bạn không thể chọn đăng ký đã phát hành để sửa", "error");
    // }
  }

  delete(data) {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    // if (this.selectedItems[0].release_status == "CHUA_TB_THUE") {
    const ids = data ? data.id : this.selectedRows.join(',');
    var result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.notifyService.delete(ids).subscribe(rs => {
          if (rs.status === "success") {
            Library.notify(this._translate.NOTIFY.delete_success, 'success');
            this.loadList();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    });
    // } else {
    //   Library.notify("Bạn không thể chọn đăng ký đã phát hành để xóa", "error");
    // }
  }

  huySo(data) {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    let rowData = data ? data : this.selectedItems[0];
    if (rowData.release_status == "DA_TB_THUE") {
      const initialState = {
        rowData: rowData,
        templates: this.templates,
      };
      this.bsModalRef = this.modalService.show(CancelNumberComponent, { class: 'modal-lg full-modal', initialState });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          this.dataSource = {};
          this.loadList();
        }
      });
    } else if (rowData.release_status == "DA_HUY_SO") {
      Library.notify(this._translate.NOTIFY.cancel_number, "error");
    } else {
      Library.notify("Bạn không thể chọn đăng ký chờ phát hành để hủy số.", "error");
    }
  }

  xuatToKhai(data) {
    let rowData = data ? data : this.selectedItems[0];

    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    Library.showloading();
    // let sub = this.notifyService.getDMTinhThanh().subscribe(rs => {
    //   if (rs.status == "success") {
    //     const initialState = {
    //       rowData: rowData,
    //       templates: this.templates,
    //       provinces: rs.data.DM_TINH_THANH_CQHC
    //     };
    //     this.bsModalRef = this.modalService.show(DeclarationComponent, { class: 'modal-lg', initialState });
    //   } else {
    //     Library.notify(rs.message, "error");
    //   }
    // }, err => { }, () => {
    //   Library.hideloading();
    // });


    var _data = {
      id: rowData.id,
      code: "1",
      // province_code: this.frmData.province_code,
      // province_name: province_name,
      // district_code: this.frmData.district_code,
      // district_name: district_name,
      // commune_name: commune_name,
      // commune_code: this.frmData.commune_code,
      home_address: this.authService.getCurrentUser().account.business_address,
    };

    Library.showloading();
    let sub = this.notifyService.xuatToKhai(_data).subscribe(rs => {
      if (rs.status == "success") {
        console.log(rs);
        let data = rs.data.invoice_release;
        Helper.dowloadFile(data.file_name, 'zip', data.file_content);
      } else {
        Library.notify(rs.message, "error");
      }
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }

  xuatExcel(data) {
    if (this.clickCurent)
      document.getElementById(this.clickCurent).classList.remove('show');
    let ids = data ? data.id : this.selectedRows.join(',');
    Library.showloading();
    let sub = this.notifyService.export(ids).subscribe(rs => {
      if (rs.status == "success") {
        Helper.dowloadFile(rs.data.invoice_release.file_name, 'xlsx', rs.data.invoice_release.file_content);
      } else {
        Library.notify(rs.message, "error");
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }
  onContentReady(e){
    if(e){
      const dataSource = this.dataGrid.instance.getDataSource();
      this.pageSize = dataSource.items().length 
    }
  }
}
