import { Component, OnInit } from '@angular/core';

import { NotifyService } from '@modules/release-notify/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { CancelModel } from '../../shared';
import { Library, Helper, AuthService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'app-cancel-number',
  templateUrl: './cancel-number.component.html',
  styleUrls: ['./cancel-number.component.scss']
})
export class CancelNumberComponent implements OnInit {

  frmData: CancelModel;
  templates: any;
  ignores: any = [];
  rowData: any;
  formatNumber = Helper.numberInvoiceFormatDev;
  rangeEndNumber = [];
  _translate: any;
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private notifyService: NotifyService,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.frmData = new CancelModel();
    this.frmData.fillData(this.rowData, this.ignores);
    this.rangeEndNumber[0] = this.frmData.begin_number;
    this.rangeEndNumber[1] = this.frmData.cancelTo;
    this.frmData.end_number = this.rowData.end_number;
  }

  onSubmit(e) {
    let data = { ...this.frmData };
    ['template_id', 'amount_release', 'cancelFrom', 'cancelTo', 'used_amount', 'end_number'].forEach(function (k) {
      delete data[k];
    });
    var result = Library.confirm(this._translate.CONFIRM.cancel_number, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.notifyService.ignore(data).subscribe(rs => {
          if (rs.status == "success") {
            Library.notify(this._translate.NOTIFY.cancel_number_success, 'success');
            this.bsModalRef.content.successSaveItem = "success";
            this.bsModalRef.hide();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    });
    console.log(this.frmData);
  }

}
