import {Component, OnInit} from '@angular/core';

import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {Subscription} from 'rxjs/Subscription';
import * as moment from 'moment';
import {VatService} from '@modules/vat/shared/vat.service';
import {AuthService, Helper, Library, SocketService} from '@core/index';
import {NotifyService} from '@modules/release-notify/shared';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {InfoUnitService} from '@modules/info-unit/shared';


@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.scss']
})
export class DocumentComponent implements OnInit {

  currentUser: any;
  formAdd: any;
  formDoc: any;
  templates: any;
  confirmFile: any;
  ioSignature: Subscription;
  disabledInputHopDong: boolean = false;
  rowData: any;
  configs: any;
  formatNumber: string = Helper.numberInvoiceFormatDev;
  isKyTapTrung: boolean = false;
  dataToken: any;
  formDataToken: FormGroup;
  dataAccount: any;
  dataTokenCli: any;

  constructor(
    private vatService: VatService,
    public bsModalRef: BsModalRef,
    private authService: AuthService,
    private notifyService: NotifyService,
    private socketService: SocketService,
    private fb: FormBuilder,
    private infoUnitService: InfoUnitService,
  ) {
  }

  ngOnInit() {
    this.formDataToken = this.fb.group(this.notifyService.formUserToken);
    this.dataToken = JSON.parse(localStorage.getItem('currentUser'));
    this.dataAccount = this.dataToken.account;

    this.formDataToken.patchValue({
      cert_serial: this.dataToken.account.cert_serial,
      cert_provider: this.dataToken.account.cert_provider,
      cert_valid_from: this.dataToken.account.cert_valid_from,
      cert_valid_to: this.dataToken.account.cert_valid_to,
      cert_content: this.dataToken.account.cert_content,
      icc_token: this.dataToken.account.icc_token,
    });
    //connect web socket
    const sub2 = this.vatService.getConfig().subscribe(rs => {
      if (rs.status == 'success') {
        this.configs = rs.data.configs;
        rs.data.configs.forEach(item => {
          if (item.name == 'integrate_parameter') {
            //this.isKyTapTrung = JSON.parse(item.value).concentrate_signature.is_concentrate_signature;
            let config = JSON.parse(item.value);
            this.isKyTapTrung = config.concentrate_signature.is_concentrate_signature;
          }
        });
      } else {
        // Library.notify('Có lỗi khi tải cấu hình', 'error');
      }
    }, err => {
    }, () => {
      sub2.unsubscribe();
    });
    this.connectWebsocket();

    this.currentUser = this.authService.getCurrentUser();
    this.formDoc = JSON.parse(JSON.stringify(this.formAdd));
    const van_account_info = this.currentUser.account.van_account_info;
    if (van_account_info.contract_date) {
      this.formDoc.contract_date = moment(van_account_info.contract_date).format('YYYY-MM-DD');
    }
    this.formDoc.contract_number = van_account_info.contract_number;

    if (van_account_info.register_type === 'TRUC_TIEP' && van_account_info.contract_number) {
      this.disabledInputHopDong = true;
    } else {
      this.disabledInputHopDong = false;
    }

    this.templates.some(item => {
      if (item.value === this.formDoc.template_id) {
        this.formDoc.template_code = item.template_code;
        this.formDoc.invoice_series = item.invoice_series;
      }
    });
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      console.log(data);
      console.log(this.dataAccount);
      if (data.SvcName === 'efy_Sign') {
        this.formDoc.confirm_file = data.Data.Content;
        if (this.dataAccount.cert_serial !== null) {
          if (this.dataAccount.cert_serial === data.Cert.SerialNumber) {
            if (this.rowData) {
              this.edit();
            } else {
              this.add(this.formDataToken.value, false);
            }
          } else {
            Library.notify('Chữ ký số không đúng', 'error');
          }
        } else {
          this.formDataToken.patchValue({
            cert_serial: data.Cert.SerialNumber,
            cert_provider: this.convertIssuer(data.Cert.Issuer),
            cert_valid_from: this.convertDate(data.Cert.DateFrom),
            cert_valid_to: this.convertDate(data.Cert.DateTo),
            cert_content: data.Cert.CertContent,
            icc_token: ''
          });
          if (this.rowData) {
            this.edit();
          } else {
            this.add(this.formDataToken.value, true);
          }
        }
      }
    });
  }

  convertIssuer(str) {
    return str;
  }

  convertDate(str) {
    return moment(str).format('YYYY-MM-DD HH:mm:ss');
  }

  updateAccount(id, data) {
    Library.showloading();
    const dataAcount = {
      account: {
        name: data.name,
        account_type: data.account_type,
        tax_province_code: data.tax_province_code,
        tax_district_code: data.tax_district_code,
        business_address: data.business_address,
        transaction_address: data.transaction_address,
        phone: data.phone,
        email: data.email,
        fax: data.fax,
        website: data.website,
        surrogate_name: data.surrogate_name,
        surrogate_position: data.surrogate_position,
        submitted_person_name: data.submitted_person_name,
        contact_person_name: data.contact_person_name,
        contact_mobile: data.contact_mobile,
        tax_department: data.tax_department,
        company_type: data.company_type,
        bank_name: data.bank_name,
        bank_account_number: data.bank_account_number,
        company_email: data.company_email,
        cert_serial: data.cert_serial,
        cert_valid_from: data.cert_valid_from,
        cert_valid_to: data.cert_valid_to,
        cert_provider: data.cert_provider,
        cert_content: data.cert_content,
        icc_token: data.icc_token,
        report_period: data.report_period,
      }
    };
    this.infoUnitService.updateInfo(dataAcount, id).subscribe(
      value => {
        if (value.status === 'success') {
          this.dataToken.account = value.data.account;
          localStorage['currentUser'] = JSON.stringify(this.dataToken);
          Library.notify('Thêm mới thông báo phát hành thành công', 'success');
          this.bsModalRef.content.successSaveItem = 'success';
          this.bsModalRef.hide();
        } else {
          Library.notify('Thêm mới thông báo phát hành thành công', 'success');
          this.bsModalRef.content.successSaveItem = 'success';
          this.bsModalRef.hide();
        }
      }, error => {
        Library.notify('Thêm mới thông báo phát hành thành công', 'success');
        this.bsModalRef.content.successSaveItem = 'success';
        this.bsModalRef.hide();
      },
      () => Library.hideloading()
    );
  }

  add(dataSign, isUpdate) {
    const data = JSON.parse(JSON.stringify(this.formDoc));
    ['template_code', 'invoice_series'].forEach(function (k) {
      delete data[k];
    });

    const dataUser = this.dataAccount;
    dataUser.cert_serial = dataSign.cert_serial;
    dataUser.cert_valid_from = dataSign.cert_valid_from;
    dataUser.cert_valid_to = dataSign.cert_valid_to;
    dataUser.cert_provider = dataSign.cert_provider;
    dataUser.cert_content = dataSign.cert_content;
    dataUser.icc_token = dataSign.icc_token;
    Library.showloading();
    this.notifyService.addNotify(data, dataSign).subscribe(rs => {
      if (rs.status === 'success') {
        if (isUpdate) {
          this.updateAccount(dataUser.id, dataUser);
        } else {
          Library.notify('Thêm mới thông báo phát hành thành công', 'success');
          this.bsModalRef.content.successSaveItem = 'success';
          this.bsModalRef.hide();
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  edit() {
    let data = JSON.parse(JSON.stringify(this.formDoc));
    ['template_code', 'invoice_series'].forEach(function (k) {
      delete data[k];
    });
    Library.showloading();
    this.notifyService.edit(this.rowData.id, data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Sửa thông báo phát hành thành công', 'success');
        this.bsModalRef.content.successSaveItem = 'success';
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  viewFile() {
    Library.showloading();
    this.notifyService.getFileReportPreview(this.formDoc).subscribe(rs => {
      if (rs.status == 'success') {
        let data = rs.data.invoice_release;
        Helper.dowloadFile(data.file_name, 'pdf', data.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  onSubmit(e) {

    if (moment(this.formDoc.release_date).add(2, 'days') > moment(this.formDoc.start_using_date)) {
      Library.notify('Ngày bắt đầu sử dụng phải lớn hơn ngày phát hành tối thiểu 2 ngày', 'error');
      return false;
    }

    Library.showloading();
    this.notifyService.getFileReportPreview(this.formDoc).subscribe(rs => {
      if (rs.status === 'success') {
        const data = rs.data.invoice_release;
        this.formDoc.confirm_file = rs.data.invoice_release.file_content;
        if (this.isKyTapTrung) {
          console.log(1);
        } else {
          this.socketService.kiFile(() => this.connectWebsocket(), data.file_content, data.file_name, 'pdf', true, null, 'ALIGN_LEFT', 'PAGE_FIRST');
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  disabledHopDong() {
    if (this.currentUser.user.is_super_admin == '1') {
      return false;
    } else {
      let info = this.currentUser.account.van_account_info;
      if (info.register_type == 'TRUC_TIEP' && info.contract_number) {
        return true;
      }
      return false;
    }
  }

}
