import { Component, OnInit, ViewChild } from '@angular/core';

import * as moment from 'moment';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DocumentComponent } from '../document/document.component';
import { NotifyModel, NotifyService } from '@modules/release-notify/shared';
import { Library, Helper, AuthService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  registerRadioItems = [
  ];
  formatNumber: string = Helper.numberInvoiceFormatDev;
  rowData: any;
  templates: any;
  frmData: NotifyModel = new NotifyModel();
  showSaveButton: boolean = true;
  showViewButton: boolean = false;
  minStartUsingDate: string = moment(new Date()).add(2, 'days').format("YYYY-MM-DD");
  _translate: any;
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private notifyService: NotifyService,
    private modalService: BsModalService,
    public bsModalDocument: BsModalRef,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.registerRadioItems = [
      {
        text: this._translate.CONTROLL.LABEL.wait_for_release,
        value: "CHUA_TB_THUE"
      },
      {
        text: this._translate.CONTROLL.LABEL.released,
        value: "DA_TB_THUE"
      },
    ];
  }

  ngOnInit() {
    if (this.rowData) {
      this.frmData.fillData(this.rowData);
      let currentUser = this.authService.getCurrentUser();
      if (this.rowData.release_status == 'DA_TB_THUE' || this.rowData.release_status == 'DA_HUY_SO') {
        this.showSaveButton = false;
      }
      if (currentUser.user.is_super_admin == '1') {
        this.showSaveButton = true;
      }

      if (this.rowData.confirm_file) {
        this.showViewButton = true;
      }
    }
  }

  a() {
    console.log(this.minStartUsingDate);
    // console.log(this.dxForm);
  }

  refeshValidate = (e) => {
    this.minStartUsingDate = moment(e.value).add(2, 'days').format("YYYY-MM-DD");
  }

  changeNumber = (e) => {
    this.frmData.info.end_number = this.frmData.info.begin_number + e.value - 1;
  }

  templateChange = (e) => {
    this.templates.forEach(item => {
      if (item.value == e.value) {
        this.frmData.info.begin_number = item.end_number + 1;
        this.frmData.info.end_number = item.end_number + this.frmData.info.amount;
      }
    });
  }

  // onSubmit(e) {
  //   let nameTemplate = "";
  //   this.templates.forEach(item => {
  //     if (item.value == this.frmData.info.template_id) {
  //       nameTemplate = item.name;
  //     }
  //   });

  //   console.log(this.frmData.info);
  //   if (this.frmData.info.release_status == "DA_TB_THUE") {
  //     var result = Library.confirm(`Bạn có chắc chắn "${nameTemplate}" đã đăng ký với cơ quan thuế không?`, "Thông báo");
  //     result.then((dialogResult) => {
  //       if (dialogResult) {
  //         // this.hanle();
  //         const initialState = {
  //           formAdd: this.frmData,
  //         };

  //         this.bsModalDocument = this.modalService.show(DocumentComponent, {class: 'modal-lg full-modal', initialState});
  //         this.modalService.onHide.subscribe(() => {
  //           if (this.bsModalRef.content.successSaveItem) {

  //           }
  //         });
  //       }
  //     });
  //   } else {
  //     this.hanle();
  //   }
  // }

  onSubmit(e) {
    if (this.frmData.info.release_status == "DA_TB_THUE") {
      // this.hanle();
      const initialState = {
        formAdd: this.frmData.info,
        templates: this.templates,
        rowData: this.rowData

      };

      this.bsModalDocument = this.modalService.show(DocumentComponent, {
        backdrop: 'static',
        keyboard: false,
        class: 'modal-lg full-modal', initialState
      });
      let subModal = this.modalService.onHide.subscribe(() => {
        if (this.bsModalDocument.content.successSaveItem) {
          this.bsModalRef.content.successSaveItem = "success";
          this.bsModalRef.hide();
          localStorage.removeItem('new_member');
        }
        subModal.unsubscribe();
      });
    } else {
      this.hanle();
    }
  }

  hanle() {
    if (this.rowData) {
      this.edit();
    } else {
      this.add();
    }
  }

  add() {
    Library.showloading();
    this.notifyService.add(this.frmData.info).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Thêm mới thành công', 'success');
        this.bsModalRef.content.successSaveItem = "success";
        this.bsModalRef.hide();
        localStorage.removeItem('new_member');
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  edit() {
    Library.showloading();
    this.notifyService.edit(this.rowData.id, this.frmData.info).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Sửa thành công', 'success');
        this.bsModalRef.content.successSaveItem = "success";
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  viewDocument() {
    Helper.dowloadFile('BM06_BB_XAC_NHAN_HOAN_THANH_DKDV.pdf', 'pdf', this.rowData.confirm_file);
    // alert(1);
    // let win = window.open("hello","_blank");
    // win.document.write(`<body style="margin: 0px;"><embed width="100%" height="100%" src="data:application/pdf;base64,${this.rowData.confirm_file}"></body>`)
  }

}
