import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

//library
import {devextremeModule} from '@core/index';

//general
import {routing} from './release-notify.routing';
import {VatService} from '@modules/vat/shared/vat.service';
import {SharedModule} from '@shared/index';
import {IndexComponent} from './components/index/index.component';
import {NotifyService} from './shared';
import {AddComponent} from './components/add/add.component';
import {CancelNumberComponent} from './components/cancel-number/cancel-number.component';
import {DeclarationComponent} from './components/declaration/declaration.component';
import {DocumentComponent} from './components/document/document.component';
import {vTranslateModule} from '@core/module/v-translate.module';
